import VueRouter from 'vue-router'
import App from './App.vue'
import mainContent from './components/main-content.vue'
import forum from './components/forum.vue'
import news from './components/news.vue'
import information from './components/information.vue'
import promotions from './components/promotions.vue'
import qas from './components/qas.vue'
import tavern from './components/tavern.vue'
import clans from './components/clans.vue'
import marketplace from './components/marketplace.vue'
import support from './components/support.vue'
import bugs from './components/bugs.vue'
import SignIn from './components/SignIn.vue'
import SignUp from './components/SignUp.vue'


Vue.use(VueRouter);

const router = new VueRouter({
  routes: [
    { path: '/', component: mainContent },
    { path: '/forum', component: forum},
    { path: '/forum/news', component: news},
    { path: '/forum/information', component: information},
    { path: '/forum/promotions', component: promotions},
    { path: '/forum/qas', component: qas},
    { path: '/forum/tavern', component: tavern},
    { path: '/forum/clans', component: clans},
    { path: '/forum/marketplace', component: marketplace},
    { path: '/forum/support', component: support},
    { path: '/forum/bugs', component: bugs},
    { path: '/signIn', component: SignIn},
    { path: '/signUp', component: SignUp}
  ],
  mode: 'history'
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
})




