<template>
  <div id="app">
    <div class="container-fluid header wrapper">
      <div class="container">
        <div class="row">
          <div class="col">
            <router-link to="/">
              <button type="button" class="btn btn-primary"></button>
            </router-link>
            <router-link to="/forum">
              <button type="button" class="btn btn-primary"></button>
            </router-link>
          </div>
          <div class="col header-signInUp-wrapper">
            <div v-if="loadSignComplete() === false">
                <button type="button" class="btn btn-primary" @click="switchSign('sign-in')"></button>
                <button type="button" class="btn btn-primary" @click="switchSign('sign-up')"></button>
            </div>
            <div v-else>
              <span> {{email}} </span>
              <button type="button" class="btn btn-primary" @click="signOut()"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
      <div class="container-fluid main-content wrapper">
        <sign-in class="userForms" v-if="sign === 'sign-in'" @addUser="isMainPage = $event.mainPage, signComplete = $event.complete, sign = $event.sign, email = $event.email, uid = $event.uid"></sign-in>
        <sign-up class="userForms" v-if="sign === 'sign-up'" @regSucces="sign = $event"></sign-up>
        <router-view></router-view>
      </div>
      <div class="container-fluid footer wrapper">Footer wrapper </div>
  </div>
</template>

<script>

import mainContent from './components/main-content.vue'
import forum from './components/forum.vue'
import signIn from './components/SignIn.vue'
import signUp from './components/SignUp.vue'
export default {
  name: 'app',
  data () {
    return {
      sign: '',
      email: localStorage.getItem('email'),
      uid: localStorage.getItem('uid'),
      signComplete: localStorage.getItem('complete'),
    }
  },
  components: {
    mainContent,
    forum,
    signIn,
    signUp
  },
  methods: {
    switchSign(currentSign) {
      if (this.sign === currentSign) {
        this.sign = '';
      }
      else {
        this.sign = currentSign;
      }
    },
    loadSignComplete() {
      if (localStorage.getItem('complete')) {
        return true
      }
      else return false
    },
    signOut() {
      localStorage.clear();
      this.email = '';
      this.uid = '';
      signComplete = false;
    }
  }
}



</script>

<style lang="css">
  .wrapper {
    border: 2px solid black;
  }
  .header {
    min-height: 150px;
  }
  .main-content {
    min-height: 650px;
  }
  .footer {
    min-height: 150px;
  }
  .userForms {
    position: absolute;
    left: 30%;
    top: 15%;
    background: gray;
    min-width: 50%;
    border: 2px solid black;
  }
</style>



