<template>
    <div>
        <router-link to="/forum/">back</router-link>
        <button type="button" class="btn btn-primary" @click="addPost">new post</button>
        <ul v-if="createNew === false && postOpened === false"  class="posts-list">
            <li v-for="post in posts" v-bind:key="post.id" @click="openPost(post.id)">
                <div class="container-fluid post">
                    <div class="row">
                        <div class="col">
                            {{ post.author }}
                        </div>
                        <div class="col">
                            {{ post.title }}
                        </div>
                        <div class="col">
                            date/time
                        </div>
                    </div>
                </div>
            </li>
        </ul>
        <div v-if="postOpened" class="container-fluid openedPost">
            <h3>{{post.title}}</h3>
            <div class="row">
                <div class="col-sm-3">
                    {{post.author}}
                </div>
                <div class="col">
                    {{post.body}}
                </div>
            </div>
        </div>
        <div v-if="createNew" class="container-fluid newPost">
            <form class="mt-5" @submit.prevent="sendNewPost">
                <div class="form-group">
                    <input class="form-control" type="text" placeholder="  " v-model="newPostTitle">
                    <textarea class="form-control" rows="10" v-model="newPostBody"></textarea>
                    <button type="submit" class="btn btn-primary"></button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>

export default {
    name: 'app',
    data () {
        return {
            posts: [],
            post: {},
            test1: 'tested',
            createNew: false,
            newPostBody: '',
            newPostTitle: '',
            postOpened: false
        }
    },
    mounted: function() {
        this.$nextTick(function () {
                        var starCountRef = firebase.database().ref('forum/' + 'news/');
                        starCountRef.on('value', (snapshot) => {
                            let obj = snapshot.val();
                            this.dataFromBase(obj);
                            //  Object.keys(obj).length
                            //  obj[Object.keys(obj)[0]]
                            //   Object.keys(obj)[0]
                        });
        });
    },
    methods: {
        dataFromBase(obj) {
            for (let i = 0; i < Object.keys(obj).length; i++) {
                obj[Object.keys(obj)[i]].id = Object.keys(obj)[i];
                let post = obj[Object.keys(obj)[i]];
                this.posts.push(post);
            }
        },
        addPost() {
            this.createNew = !this.createNew;
        },
        sendNewPost() {
            console.log('send new post');
            if ((this.newPostBody != '') && (this.newPostTitle != '')) {
                this.addPost();
            }
            
              // A post entry.
            var postData = {
                author: localStorage.email,
                uid: localStorage.uid,
                body: this.newPostBody,
                title: this.newPostTitle,
            };

            // Get a key for a new Post.
            var newPostKey = firebase.database().ref().child('/news').push().key;

            // Write the new post's data simultaneously in the posts list and the user's post list.
            var updates = {};
                updates['/forum/news/' + newPostKey] = postData;
             //   updates['/user-posts/' + uid + '/' + newPostKey] = postData;

            return firebase.database().ref().update(updates);
        },
        openPost(id) {
            for (let i = 0; i < this.posts.length; i++) {
                if (this.posts[i].id === id) {
                    console.log(this.posts[i]);
                    this.post = this.posts[i];
                    this.postOpened = true;
                }
            }
        }
    }
}
</script>

<style lang="css">
.posts-list {
    list-style-type: none;
}
.post {
    border: 1px solid black;
    min-height: 40px;
    margin: 2px;
}
.newPost {
    min-height: 300px;
    border: 1px solid black;
}
</style>


